




























































































































































































































export default {
	data() {
		return {
			alertImg : false, //预览弹窗是否显示,默认不显示
			alertProtocol : false, //查看协议
			radio : false
		};
	},
	methods:{
		lookPreImg() {
			this.alertImg = true;
			console.log('点击预览');
		},
		closeImgAlert() {
			this.alertImg = false;
			console.log('点击了关闭');
		},
		lookProtocol() {
			// this.alertProtocol = true;
			uni.navigateTo({
				url : '../protocol/protocol'
			});
			console.log('点击查看协议');
			
		},
		closeProtocol() {
			this.alertProtocol = false;
			console.log('点击关闭协议');
		},
		goMain_index() {
			if(this.radio == true) {
				// 修改step 并保留用户的身份证信息
				// 已经模拟身份证信息保存了
				try {
					const openid = uni.getStorageSync('openid');
					if (openid) {
						uni.request({
							url: global.host + 'Zhu/editCurrentStep',
							method: 'GET',
							data: {
								openid : openid,
								current_step : 3 // 3代表选择套餐阶段
							},
							success: res => {
								// 在跳转之前,要保留用户的信息
								uni.request({
									url: global.host + 'Zhu/changeIdCard',
									method: 'GET',
									data: {
										openid : openid,
										user_name : '张彬',
										id_card_number : '411328199408230055',
										id_card_sex : 1,
										id_card_nation : 1,
										id_card_address : '上海宝山新二路55号',
										id_card_front : 'https://wx.qlogo.cn/mmopen/vi_32/vHvGVjRQ5MUwy7Osj6C1pkaQghVh6vQ4IfCJicPa4NIpax0URult0eyic0T398nAyR73vricMfBAr3IXYbfsPxicAw/132',
										id_card_back : 'https://wx.qlogo.cn/mmopen/vi_32/vHvGVjRQ5MUwy7Osj6C1pkaQghVh6vQ4IfCJicPa4NIpax0URult0eyic0T398nAyR73vricMfBAr3IXYbfsPxicAw/132'
									},
									success: res => {
										// 保留成功以后去跳转到主页
										uni.redirectTo({
											url: '../main_index/main_index?from=registrationInfo'
										});
									},
									fail: () => {},
									complete: () => {}
								});
								
							},
							fail: () => {},
							complete: () => {}
						});
					}
				} catch (e) {
					// error
					console.log('error888',e);
				}
			} else {
				uni.showToast({
				title: '请先同意筑商协议...',
				icon : 'none',
				});
			}
		
		},
		radioChange() {
			this.radio = !this.radio;
		}
	}
}
